.split-letters{
	margin-top: 20px;
	font-size: 20px;
	display: flex;
	flex-wrap: wrap;

	&>div {
		white-space: pre-line;
		margin-right: 0.4em;
		cursor: default;
	}
}
.split-words {
	margin-top: 20px;
	font-size: 20px;
	display: flex;
	flex-wrap: wrap;

	&>div {
		white-space: pre-line;
		float: left;
		margin-right: 5px;
		cursor: default;
	}
}

.letter-measure {
	margin-right: 0 !important;
	cursor: default;
}
.blank {
	margin-right: 0px !important;
	white-space: pre !important;
}